import { BuiltInProfiles, getTargetDriveFromProfileId } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useCircles from '../../../hooks/circles/useCircles';
import CirclesIcon from '../../Icons/Circles/Circles';
import { CircleDefinition } from '../../../provider/connections/CirlceDataTypes';
import ActionButton from '../../Buttons/ActionButton';
import CircleDialog from '../../Dialog/CircleDialog/CircleDialog';
import PageMeta from '../../Layout/PageMeta/PageMeta';

const Circles = () => {
  const {
    fetch: { data: circles, isLoading: isCirclesLoading },
  } = useCircles();

  const [isOpenCreate, setIsOpenCreate] = useState(false);

  if (isCirclesLoading) {
    <>Loading</>;
  }

  return (
    <>
      <PageMeta
        icon={CirclesIcon}
        title={t('My Circles')}
        actions={
          <>
            <ActionButton icon="plus" onClick={() => setIsOpenCreate(true)}>
              {t('Add Circle')}
            </ActionButton>
          </>
        }
      />
      <section className="-my-4">
        {circles && !isCirclesLoading
          ? circles.map((circleDef) => (
              <CircleCard circleDefinintion={circleDef} key={circleDef.id} className="py-4" />
            ))
          : null}
      </section>
      <CircleDialog
        title={t('Create new Circle')}
        isOpen={isOpenCreate}
        defaultValue={{
          name: '',
          description: '',
          drives: [
            {
              drive: getTargetDriveFromProfileId(BuiltInProfiles.StandardProfileId),
              permission: 15,
            },
          ],
          permissions: {
            keys: [15],
          },
        }}
        onConfirm={() => {
          setIsOpenCreate(false);
        }}
        onCancel={() => {
          setIsOpenCreate(false);
        }}
      />
    </>
  );
};

const CircleCard = ({
  circleDefinintion,
  className,
}: {
  circleDefinintion: CircleDefinition;
  className: string;
}) => {
  return (
    <div className={`${className}`}>
      <Link to={`/owner/circles/${encodeURIComponent(circleDefinintion.id)}`} className="contents">
        <div
          className={`h-full rounded-md border-[1px] border-gray-200 border-opacity-60 bg-white transition-colors hover:bg-slate-100 dark:border-gray-800 dark:bg-gray-800 dark:hover:bg-slate-700`}
        >
          <h2 className="bg-slate-100 p-4 text-xl dark:bg-slate-700 dark:text-slate-200">
            {circleDefinintion.name}
          </h2>
          <div className="p-4">
            <p>{circleDefinintion.description}</p>
            <p className="text-xs">
              Created on: {new Date(circleDefinintion.created).toLocaleDateString()}
            </p>
          </div>
        </div>
      </Link>
    </div>
  );
};

export default Circles;
